:root {
  --background: #f1f1f1;
  --dark: black;
  --middle: #0f3d3e;
  --light: #e2dcc8;
}
html {
  background-color: var(--middle);
}
h1 {
  text-align: center;
  color: var(--light);
}
.App {
  background-color: var(--light);
  margin: 5%;
  height: 100%;
}

.ProjectList {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 45% 45%;
  row-gap: 5%;
  grid-auto-flow: row;
  border: solid var(--dark) 2px;
  padding-bottom: 2%;
}

.Project {
  border: solid var(--dark) 2px;
  margin: 5%;
  background-color: var(--background);
  position: relative;
  height: 100%;
  text-align: center;
}

.Text {
  color: var(--background);
  margin: 5%;
}

.Img {
  position: absolute;
  z-index: 1;
  width: 40%;
  left: 30%;
  display: block;
}

.Img:hover {
  opacity: 0;
}

.Project:hover {
  background-color: var(--middle);
  color: var(--background);
}
